.container {
  margin-left: auto;
  margin-right: auto;

  padding-left: 20px;
  padding-right: 20px;

  @media screen and (max-width: 768px) {
    max-width: 768px;
  }

  @media screen and (min-width: 769px) {
    padding-left: 32px;
    padding-right: 32px;

    width: 769px;
  }

  @media screen and (min-width: 1280px) {
    padding-left: 16px;
    padding-right: 16px;

    width: 1280px;
  }
}

.header__container {
  position: relative;
}

.hero__container {
  position: relative;
}
